function getGoodsInfo(param) {
	return {
		types: [GOODS_LOADING, GOODS_LOAD_SUCCESS, GOODS_LOADING_FAIL],
		promise: function promise(client) {
			return client.get('/item/platformCategoryAttributeController/queryPlatformCategoryAttributeByAttrID', {
				params: param
			});
		}
	};
}
//获取销售属性
function getSaleInfo(param) {
	return {
		types: [GOODS_SALE, GOODS_SALE_SUCCESS, GOODS_SALE_FAIL],
		promise: function promise(client) {
			return client.get('item/platformCategoryAttributeController/querySaleAttr', {
				params: param
			});
		}
	};
}
//获取类目属性
function getBrandInfo(param) {
	return {
		types: [GOODS_BRAND, GOODS_BRAND_SUCCESS, GOODS_BRAND_FAIL],
		promise: function promise(client) {
			return client.get('item/platformCategoryAttributeController/queryCategoryAttr', {
				params: param
			});
		}
	};
}
//获取计量单位
function getUnitInfo(param) {
	return {
		types: [GOODS_UNIT, GOODS_UNIT_SUCCESS, GOODS_UNIT_FAIL],
		promise: function promise(client) {
			return client.get('item/category/queryUnitByCategoryId', {
				params: param
			});
		}
	};
}

//获取发布者信息 //先写死
function getPublisherInfo(param) {
	return {
		types: [GOODS_PUBLISHER, GOODS_PUBLISHER_SUCCESS, GOODS_PUBLISHER_FAIL],
		promise: function promise(client) {
			return client.get('platform-passport/itemPublish/queryPublishUser');
		}
	};
}
//获取运营者信息
function getOperatorInfo(param) {
	return {
		types: [GOODS_OPERATORS, GOODS_OPERATORS_SUCCESS, GOODS_OPERATORS_FAIL],
		promise: function promise(client) {
			return client.get('platform-passport/user/queryAllAdminUser', {
				params: param
			});
		}
	};
}
// 更改后属性信息
function postSaleInfo(data) {
	return {
		type: GOODS_SALEINFO_AFTER_SUCCESS,
		data: data
	};
}
function uploadPrams(data) {
	return {
		type: UPLOADPRAMAS,
		data: data
	};
}
function release(param, url) {
	return {
		types: [GOODS_LOADING, GOODS_LOAD_SUCCESS, GOODS_LOADING_FAIL],
		promise: function promise(client) {
			return client.post(url, {
				//data: "itemPublishVo=" + JSON.stringify(param)
				data: param
			});
		}
	};
}


