var PICUP = 'item/brand/PICUP';
var PICUP_SUCCESS = 'item/brand/PICUP_SUCCESS';
var PICUP_FAIL = 'item/brand/PICUP_FAIL';

function brandup(platformId, pictures) {
  return {
    types: [PICUP, PICUP_SUCCESS, PICUP_FAIL],
    promise: function promise(client) {
      return client.post('/item/brandController/uploadImgFile', { data: { platformId: platformId, pictures: pictures } });
    }
  };
}

